.body__start-animation {
  /* background: linear-gradient(
    to left,
    rgba(22, 105, 188, 1),
    rgba(23, 0, 153, 1)
  ); */
  background-color: rgba(23, 0, 153, 1);
  transition: background-color linear;

  animation: startAnimationBody 20s infinite;
}

@keyframes startAnimationBody {
  25% {
    background-color: rgb(36, 153, 0);

    /* background: linear-gradient(to left, rgb(255, 94, 0, 1), rgb(165, 52, 0)); */
  }
  50% {
    background-color: rgb(216, 160, 6);

    /* background: linear-gradient(
      to left,
      rgb(30, 144, 255, 1),
      rgb(0, 206, 209, 1)
    ); */
  }
  75% {
    background-color: rgb(153, 0, 13);

    /* background: linear-gradient(
      to left,
      rgb(34, 126, 142, 1),
      rgb(15, 147, 30, 1)
    ); */
  }
}

.wrapper__start-animation {
  transform-origin: top left;

  animation: startAnimationWrapper 2s ease-in-out;
}
@keyframes startAnimationWrapper {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  60% {
    opacity: 0.1;
    transform: scale(1.02);
  }
}

.header__person-animation {
  animation: moveTitle 60s infinite;
}

@keyframes moveTitle {
  0%,
  4% {
    transform: scale(1);
  }
  2% {
    transform: scale(1.05);
  }

  23%,
  28.5%,
  48%,
  51% {
    transform: scale(1) rotate(0);
  }
  26%,
  28% {
    transform: scaleX(0.4) rotate(0);
    transform-origin: 40% center;
  }
  28.3% {
    transform: scaleX(1.15);
  }
  50% {
    transform: scale(1) rotate(-3deg);
  }
  50.5% {
    transform: scale(1) rotate(1deg);
  }

  79%,
  81% {
    transform: scale(1) translateX(0px);
  }
  80% {
    transform: translateX(-30px);
  }
}
.header__letter-animation {
  display: inline-block;
  animation: rotateLetter 10s infinite;
  transform-origin: center;
}

@keyframes rotateLetter {
  0%,
  10%,
  100% {
    transform: rotate(0);
  }
  5% {
    transform: rotateX(180deg);
  }
}

/* window modal */

.modal__window-animation {
  animation: windowOpacity 1s ease-in-out;
}
@keyframes windowOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.gallery__img-animation {
  transform-origin: top;
  animation: imgAnimationGallery 1s ease-in-out;
}

@keyframes imgAnimationGallery {
  0% {
    transform: scaleY(0);
  }
}
